.App {
  text-align: center;
}


body {
  background-image: url("./image.jpg");
  background-size: cover;
  background-repeat: no-repeat;
}

.box {
  position: absolute;
  left: 20%;
  margin-top: 30px;
  width: 50%;
  height: 300px;
  background-color: black;
  padding: 2rem;
  opacity: 0.9;
}

.show {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

h1 {
  color: white;
  opacity: 1;
}

label {
  color: white;
}

h2 {
  margin-bottom: 70px;
  color: white;
}

.box1 {
  position: absolute;
  left: 15%;
  width: 65%;
  top: 450px;
  height: fit-content;
}

.ans {
  margin-bottom: 20px;
}

li {
  list-style: none;
}

h4 {
  color: #eeeeee;
}
h7{
  color: #eeeeee;
}

